@charset "utf-8";

// New Color Variables
$theme-primary: #00BFFF; // Forest Green
$theme-dark: #333333; // Keep or adjust if needed

html,
body {
    height: 100%;
    width: 100%;
}

body {
    @include serif-font;
}

hr {
    border-color: $theme-primary;
    border-width: 3px;
    max-width: 50px;
}

hr.light {
    border-color: white;
}

a {
    @include transition-all;
    color: $theme-primary;
    &:hover,
    &:focus {
        color: darken($theme-primary, 10%);
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    @include sans-serif-font;
    color: $theme-dark; // Adjust if needed
}

p {
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 20px;
}

.bg-primary {
    background-color: $theme-primary; // Forest Green
}

.bg-dark {
    background-color: $theme-dark;
    color: white;
}

.text-faded {
    color: rgba(white, .7);
}

section {
    padding: 100px 0;
}

aside {
    padding: 50px 0;
}

.no-padding {
    padding: 0;
}

// Navigation

.navbar-default {
    background-color: white;
    border-color: none;
    @include sans-serif-font;
    @include transition-all;
    
    .navbar-header .navbar-brand {
        color: $theme-primary;
        @include sans-serif-font;
        font-weight: 700;
        text-transform: none;
        
        &:hover,
        &:focus {
            color: darken($theme-primary, 10%);
        }
    }
    
    .nav {
        > li {
            > a,
            > a:focus {
                text-transform: none;
                font-weight: 700;
                font-size: 13px;
                color: $theme-dark;
                
                &:hover {
                    color: $theme-primary;
                }
            }
            &.active {
                > a,
                > a:focus {
                    color: $theme-primary !important;
                    background-color: transparent;
                    
                    &:hover {
                        background-color: transparent;
                    }
                }
            }
        }
    }
    
    @media (min-width: 768px) {
        background-color: transparent;
        border-color: rgba(white, .3);
        
        .navbar-header .navbar-brand {
            color: rgba(white, .7);
            &:hover,
            &:focus {
                color: white;
            }
        }
        
        .nav > li > a,
        .nav > li > a:focus {
            color: rgba(white, .7);
            &:hover {
                color: white;
            }
        }
        
        &.affix {
            background-color: white;
            border-color: rgba($theme-dark, .5);
            
            .navbar-header .navbar-brand {
                color: $theme-primary;
                font-size: 14px;
                
                &:hover,
                &:focus {
                    color: darken($theme-primary, 10%);
                }
            }
            
            .nav > li > a,
            .nav > li > a:focus {
                color: $theme-dark;
                &:hover {
                    color: $theme-primary;
                }
            }
        }
    }
}

// Homepage Header

header {
    position: relative;
    width: 100%;
    min-height: auto;
    @include background-cover;
    background-position: center;
    background-image: url('../img/bg.jpg');
    text-align: center;
    color: white;
    
    .header-content {
        position: relative;
        text-align: center;
        padding: 100px 15px 100px;
        width: 100%;
        
        .header-content-inner {
            h1 {
                color: #333333; // Turquoise
                font-weight: 700;
                text-transform: uppercase;
                margin-top: 0;
                margin-bottom: 0;
            }
            hr {
                margin: 30px auto;
            }
            p {
                font-weight: 300;
                color: rgba(white, .7);
                font-size: 16px;
                margin-bottom: 50px;
            }
        }
    }
    
    @media (min-width: 768px) {
        min-height: 100%;
        
        .header-content {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            padding: 0 50px;
            
            .header-content-inner {
                max-width: 1000px;
                margin-left: auto;
                margin-right: auto;
                
                p {
                    font-size: 18px;
                    max-width: 80%;
                    margin-left: auto;
                    margin-right: auto;
                }
            }
        }
    }
}

// Sections

.section-heading {
    margin-top: 0;
}

.service-box {
    max-width: 400px;
    margin: 50px auto 0;
    
    @media (min-width: 992px) {
        margin: 20px auto 0;
    }
    
    p {
        margin-bottom: 0;
    }
}

// Portfolio Box with Alignments
.portfolio-box {
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0 auto;
    text-align: center; // Centering content within the box

    img {
        width: 100%; /* Responsive image */
        height: auto; /* Maintain aspect ratio */
    }

    .portfolio-box-caption {
        color: white;
        opacity: 0;
        display: block;
        background: rgba($theme-primary, .9);
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 100%;
        @include transition-all;

        .portfolio-box-caption-content {
            width: 100%;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            padding: 15px; // Add padding for better spacing
            min-height: 150px; // Ensure enough height for content
            text-align: center; // Center the category and name

            .project-category {
                text-transform: uppercase;
                font-weight: 600;
                font-size: 14px;
                color: rgba(white, .8); // Slightly faded for emphasis
                margin-bottom: 10px; // Add margin for spacing
            }

            .project-name {
                font-size: 20px; // Increase font size for prominence
                font-weight: 700;
                color: white; // Ensure it's easily readable
                margin-bottom: 10px; // Add margin for spacing
            }

            .project-details {
                font-size: 16px; // Maintain readability
                color: rgba(white, .9); // Lightened for contrast
                text-align: justify; // Justify the details
            }
        }
    }

    &:hover {
        .portfolio-box-caption {
            opacity: 1;
        }
    }

    @media (min-width: 768px) {
        .portfolio-box-caption {
            .portfolio-box-caption-content {
                .project-category {
                    font-size: 16px;
                }
                .project-name {
                    font-size: 22px;
                }
            }
        }
    }
}

// Call-to-action
.call-to-action {
    h2 {
        margin: 0 auto 20px;
    }
}

// Bootstrap Overrides
.text-primary {
    color: $theme-primary;
}

.no-gutter > [class*='col-'] {
    padding-right: 0;
    padding-left: 0;
}

// Button Styles
.btn-default {
    @include button-variant($theme-dark, white, white);
}

.btn-primary {
    @include button-variant(white, $theme-primary, $theme-primary);
}

.btn {
    @include sans-serif-font;
    border: none;
    border-radius: 300px;
    font-weight: 700;
    text-transform: uppercase;
}

.btn-xl {
    padding: 15px 30px;
}

// Aside

aside.bg-dark {
    background-image: url('../img/bg2.jpg'); // Adjust the path as needed
    background-size: cover; // Ensure the image covers the entire section
    background-position: center; // Center the image
    color: white; // Maintain text color for contrast

    // Optional: Add padding if needed
    padding: 50px 0;
}

// Contact
#contact .fa {
    color: $theme-dark;
    font-size: 4em;
}

// Extras
// -- Highlight Color Customization
::-moz-selection {
    color: white;
    text-shadow: none;
    background: $theme-dark;
}

::selection {
    color: white;
    text-shadow: none;
    background: $theme-dark;
}

img::selection {
    color: white;
    background: transparent;
}

img::-moz-selection {
    color: white;
    background: transparent;
}

body {
    -webkit-tap-highlight-color: $theme-dark;
}
